.pins {
    position: absolute;
    width: 0px;
    height: 0px;
    left: 50%;
    top: 50%;
    z-index: 1;
    .pin{
        display: flex;
        align-items: center;
        gap: 12px;
        position: absolute;
        width: max-content;
        cursor: pointer;
        .pin-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 36px;
            height: 36px;
            border: 1px solid $cl-black;
            border-radius: 50%;
            background: $cl-white;
            img {
                width: 24px;
                height: 24px;
            }
        }
        
        .pin-title {
            transition: 0.3s ease-in-out;
            opacity: 1;
        }
    }
    .talvi-infinity-pool-pin-1 {top: 7vh;  left: 0vw;}
    .talvi-infinity-pool-pin-2 {top: -10vh;  left: -10vw;}
    .talvi-infinity-pool-pin-3 {top: -10vh;  left: 18vh;}
    .talvi-infinity-pool-pin-4 {top: 19vh;  left: -13vw;}

    .talvi-spa-pin-1 {top: 7vh;  left: 0vw;}
    .talvi-spa-pin-2 {top: -10vh;  left: -10vw;}
    .talvi-spa-pin-3 {top: -10vh;  left: 18vh;}
    .talvi-spa-pin-4 {top: 19vh;  left: -13vw;}

    .pilvi-journey-pin-1 {top: 7vh;  left: 0vw;}
    .pilvi-journey-pin-2 {top: -10vh;  left: -10vw;}
    .pilvi-journey-pin-3 {top: -10vh;  left: 18vh;}
    .pilvi-journey-pin-4 {top: 19vh;  left: -13vw;}

    .polku-journey-pin-1 {top: 7vh;  left: 0vw;}
    .polku-journey-pin-2 {top: -10vh;  left: -10vw;}
    .polku-journey-pin-3 {top: -10vh;  left: 18vh;}
    .polku-journey-pin-4 {top: 19vh;  left: -13vw;}

    .suvi-journey-pin-1 {top: 7vh;  left: 0vw;}
    .suvi-journey-pin-2 {top: -10vh;  left: -10vw;}
    .suvi-journey-pin-3 {top: -10vh;  left: 18vh;}
    .suvi-journey-pin-4 {top: 19vh;  left: -13vw;}

    .kivi-journey-pin-1 {top: 7vh;  left: 0vw;}
    .kivi-journey-pin-2 {top: -10vh;  left: -10vw;}
    .kivi-journey-pin-3 {top: -10vh;  left: 18vh;}
    .kivi-journey-pin-4 {top: 19vh;  left: -13vw;}

    .arrival-zone-pin-1 {top: 7vh;  left: 0vw;}
    .arrival-zone-pin-2 {top: -10vh;  left: -10vw;}
    .arrival-zone-pin-3 {top: -10vh;  left: 18vh;}
    .arrival-zone-pin-4 {top: 19vh;  left: -13vw;}

    .luola-journey-pin-1 {top: 10vh;  left: -1vw;}
    .luola-journey-pin-2 {top: 32vh;  left: -1vw;}
    .luola-journey-pin-3 {top: -20vh;  left: -1vw;}
    .luola-journey-pin-4 {top: 18vh;  left: 2vw;}
    
    .visible {
        opacity: 1;
        transition: 0.5s ease;
        transition-delay: 0.5s;
    }
    
    .unvisible {
        opacity: 0;
        transition: 0.1s ease;
    }
}