.navigation {
    padding: 1vw 1vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: fit-content;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 100;

    .button-container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 12px;
        height: 100%;
    }
    .box {
        width: 30px;
        height: 30px;
        background: #b1b1b1;
        border-radius: 50%;
    }
    .session-box {
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 24px;
    }
}