.concept-detail-popup {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100vh;
    background: white;
    overflow: hidden;
    transition: 0.3s ease-in-out;

    .popup-close {
        position: absolute;
        top: 24px;
        right: 48px;
        z-index: 50;
        background: #eee;
        border-radius: 50%;
    }
}

.show-popup {
    z-index: 100;
    opacity: 1;
}

.hide-popup {
    z-index: -10;
    opacity: 0;
}