.concept-detail {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    .container {
        width: 100%;
        height: 100vh;
        display: flex;
        overflow: hidden;

        
        .title-area {
            display: flex;
            flex-direction: column;
            gap: 1vw;
            width: 30%;
            padding: 4vw 2vw;
            background: white;

            .heading-large {
                margin-top: 3vw;
                text-transform: capitalize;
            }

            a {
                width: 32px;
                height: 32px;
            }
        }

        .category-area {
            width: 70%;
            display: flex;
            
            .category {
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                width: 100%;
                height: 100vh;
                padding: 2vw 2vw;
                transition: 0.3s ease; 
                cursor: pointer;

                .category-content {
                    display: flex;
                    flex-direction: column;
                    height: 12vw;
                    gap: 1vw;

                    .sub-heading,
                    .body-small {
                        color: white;
                        white-space: pre-wrap;
                    }
                }

                &:hover {
                    padding: 4vw 2vw;
                }
            }

            .background-image {
                position: absolute;
                top: 0;
                left: 0;
                z-index: -1;
                width: 70%;
                height: 100vh;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &:hover {
                .category {
                    border: none;
                }
            }
        }
    }
}