@import '../Components/BuildingOverview/floor-overview.scss';

.city {
    position: relative;

    .model-container {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        width: 100%;
        height: 100vh;
        background:#969696;
        
        .effect-layer {
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100vh;
        }

        .building {
            cursor: pointer;
        }
    }

    .city-nav {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 100;
        display: block;
    }
    .hide-city-nav {
        display: none;
    }
}
