.home {
    .container {
        width: 100%;
        height: 100vh;
        padding: 4vw 2vw;
        position: relative;
        display: flex;
        justify-content: space-between;
        overflow-wrap: hidden;

        .title-content,
        .menu-content {
            display: flex;
            align-items: center;
            width: 50%;
    
            .whole-list {
                display: flex;
                flex-direction: column;
                gap: 3vw;
                
                .middle-list {
                    display: flex;
                    flex-direction: column;
                    gap: 1vw;

                    .body-large {
                        position: relative;
                        width: max-content;
                        transition: 0.3s ease;
                        opacity: 0.8;

                        &::after {
                            position: absolute;
                            display: block;
                            content: "";
                            left: 0;
                            bottom: 0;
                            width: 0;
                            height: 2px;
                            background: white;
                            transition: 0.3s ease;
                        }

                        &:hover {
                            opacity: 1;
                            
                            &::after {
                                width: 100%;
                            }
                        }
                    }
    
                    ul {
                        display: flex;
                        flex-direction: column;
                        gap: 6px;
    
                        .item {
                            .body-medium {
                                opacity: 0.8;
                                transition: 0.3s ease;
                                text-transform: capitalize;

                                &:hover {
                                    opacity: 1;
                                    margin-left: 12px;
                                }
                            }
                        }
                    }
                }
            }
        }
    
        .title-content {
            flex-direction: column;
            justify-content: flex-end;
            align-items: flex-start;
            padding: 7.4vw 5vw;

            .title {
                font-size: 10.4167vw;
                line-height: 10.9375vw;
                color: white;
            }

            .heading-small {
                color: white;
                opacity: 0.8;
                font-style: italic;
                font-weight: 100;
            }
        }

        .menu-content {
            width: 35%;

            li a h4 {
                color: white;
            }

            li a p {
                color: #eee;
            }
        }

        .background-image {
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100vh;

            img {
                width: 100%;
                height: 100vh;
                object-fit: cover;
            }
        }
    }
}
