.navigation-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    padding: 2vh;
    width: 100%;
    height: 6vh;
    background: rgba(0, 0, 0, 0.9);
    .route-button {
        text-transform: uppercase;
        &:hover {
            opacity: 0.5;
            transition: 0.3s ease;
        }

        a {
            color: white;
        }
    }
}