.floor-elevator-container {
    display: flex;
    flex-direction: column;
    gap: 0.1vw;
    position: absolute;
    bottom: 4vw;
    left: 7vw;

    .floor-elevator {
        display: flex;
        position: relative;
        &:nth-child(1), 
        &:nth-child(8) {
            .floor-icon {
                height: 0.7vw;
            }
        }

        &:nth-child(4) {
            .floor-icon {
                height: 2.1vw;
            }
        }

        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(7) {
            .floor-icon {
                height: 1.4vw;
            }
        }

        &:nth-child(5),
        &:nth-child(6){
            .floor-icon {
                height: 2.8vw;
            }
        }

        &:nth-child(7) {
            &:after {
                position: absolute;
                display: block;
                content: "";
                top: 0;
                left: -0.5vw;
                height: 0.1vw;
                width: 5vw;
                background: #555;
            }
        }

        .floor-icon {
            width: 4vw;
            background: rgba(0, 0, 0, 0.1);
            transition: 0.3s ease;
            border: 1px solid  rgba(0, 0, 0, 0.3);;
            cursor: pointer;
            &:hover {
                background: rgba(0, 0, 0, 0.3);
            }
        }

        .number {
            position: absolute;
            top: calc(50% - 9px);
            left: 4vw;
            width: max-content;
            font-weight: 600;
            opacity: 0;
            transition: 0.3s ease;
            color: #333;
        }
        
        .highlight {
            background: rgba(0, 0, 0, 0.3);
        }

        &:hover {
            .number {
                left: 5vw;
                opacity: 1;
            }
        }
    }
}