$cl-black : #000000;
$cl-gray05 : #333333;
$cl-gray04 : #626262;
$cl-gray03 : #8F8F8F;
$cl-gray02 : #C8C8C8;
$cl-gray01 : #E4E4E4;
$cl-white : #FFFFFF;

$cl-main : #F7EEDF;

@font-face {
    font-family: DMSerifDisplay;
    src: url("./Assets/Fonts/DMSerifDisplay-Regular.ttf") format("truetype");
}

@font-face {
    font-family: Jost;
    src: url("./Assets/Fonts/Jost-VariableFont_wght.ttf") format("truetype");
}


@mixin heading-font {
    font-family: DMSerifDisplay, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
@mixin body-font {
    font-family: Jost, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
}
@mixin body-bold-font {
    font-family: Jost, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
}


@mixin title {
    font-size: 5.2083vw; // 100
    line-height: 6.25vw; // 120
    @include heading-font;
}

@mixin heading-large {
    font-size: 3.75vw; // 72
    line-height: 4.1667vw; // 80
    @include heading-font;
}

@mixin heading-medium {
    font-size: 3.125vw; // 60
    line-height: 3.75vw; // 72
    @include heading-font;
}

@mixin heading-small {
    font-size: 2.5vw; // 48
    line-height: 3.125vw; // 60
    @include heading-font;
}

@mixin sub-heading {
    font-size: 1.875vw; // 36
    line-height: 2.1875vw; // 42
    @include body-bold-font;
}



@mixin body-large {
    font-size: 1.25vw; // 24
    line-height: 1.6667vw; // 32
    @include body-font;
}
@mixin body-medium {
    font-size: 1.0417vw; // 20
    line-height: 1.4583vw; // 28
    @include body-font;
}
@mixin body-small {
    font-size: 0.9375vw; // 18
    line-height: 1.1458vw; // 22
    @include body-font;
}
@mixin micro {
    font-size: 0.7292vw; // 14
    line-height: 1.0417vw; // 20
    @include body-font;
}