.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    transition: 0.3s ease;
    cursor: pointer;
    img {
        width: 32px;
        height: 32px;
        cursor: pointer;
    }

    &:hover {
        opacity: 0.7;
    }
}