@import './HomeNavigation/home-navigation.scss';
@import './HomePopup/home-popup.scss';

.section-information {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #B19A82;

    .background-color {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100vh;
        transition: 0.3s ease;
    }

    .floor-container {
        position: relative;
        width: 16vw;
        height: 100vh;
        margin-left: 7.7vw;

            .zone {
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: absolute;
                transition: 0.3s ease;
                width: 100%;
                opacity: 0.6;
                
                .pin-title {
                    position: absolute;
                    bottom: 0.4vw;
                    left: 0;
                    display: flex;
                    align-items: center;
                    width: 100%;

                    .body-medium {
                        padding-left: 0;
                        transition: 0.3s ease;
                    }

                    &:hover {
                        .body-medium {
                            padding-left: 12px;
                        }
                    }
                }

                .floor-number {
                    p {
                        color: $cl-gray05;
                    }
                }

                .pin-line {
                    position: absolute;
                    bottom: 0;
                    width: 100%;
                    height: 0.0925vw;
                    margin: 0;
                    background: $cl-black;
                    border: none;
                    transition: 0.3s ease;
                }

                &:hover {
                    opacity: 1;
                }

                &:nth-child(1) { top: 11.4vh;}
                &:nth-child(2) { top: 17.6vh; }
                &:nth-child(3) { top: 24.7vh; }
                &:nth-child(4) { top: 35vh; }
                &:nth-child(5) { top: 54vh; }
                &:nth-child(6) { top: 72.4vh; }
                &:nth-child(7) { top: 84vh; }
                &:nth-child(8) { top: 89vh; }
            }
    }

    .section-map {
        position: relative;
        /*
        position: absolute;
        top: 0; // 8.5vh;
        left: 43.4vh;
        */
        width: 24.7vh;
        height: 100vh;
        
        .zone-highlight-container {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            

            .zone-highlight {
                width: 19vh;
                margin-left: 1.7vh;
                opacity: 0;
                transition: 0.3s ease;
            }

            .zone-highlight-0 { /* background: #FFFFFF;*/ height: 10.5vh; }
            .zone-highlight-1 { /* background: #8b8b8b;*/ height: 7vh; }
            .zone-highlight-2 { /* background: #d8b99d;*/ height: 7vh; }
            .zone-highlight-3 { /* background: #A7A7A1;*/ height: 10.5vh; }
            .zone-highlight-4 { /* background: #8d9b7d;*/ height: 18.5vh; }
            .zone-highlight-5 { /* background: #A7A8A9;*/ height: 18.9vh; }
            .zone-highlight-6 { /* background: #b19a82;*/ width: 22vh; margin-top: 2.8vh; height: 9vh; }
            .zone-highlight-7 { /* background: #707071;*/ width: 22.2vh; margin-left: 1.5vh; height: 3.7vh; }

            .show-highlight {
                opacity: 0.7;
            }
        }

        img {
            margin-top: 8.5vh;
            width: 100%;
        }
    }

    .zone-brief-container {
        position: relative;
        width: 40%;
        height: 100%;
        padding: 9vh 6vh;
        overflow: hidden;

        .zone-brief {
            display: flex;
            flex-direction: column;
            gap: 1vw;
            position: absolute;
            height: 100%;
            opacity: 0;
            transition: 0.3s ease;

            .brief-body-content {
                display: flex;
                flex-direction: column;
                gap: 0.4vw;
                .icon {
                    img {
                        width: 6vh;
                        height: 6vh;
                    }
                }

                .zone-illustration {
                    width: 100%;
                    height: 100%;
    
                    img {
                        width: 100%;
                    }
                }
            }
        }

        .show-brief {
            opacity: 1;
        }
    }
}


.fade-in {
    opacity: 1;
    transition: 0.3s;
    z-index: 100;
}

.fade-out {
    opacity: 0;
    transition: 0.3s;
    z-index: -10;
}

