.concept {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background: #2D2822;

    .concept-title {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        position: absolute;
        top: calc(50% - 4vw);
        left: calc(50% - 15vw);
        width: 30vw;
        height: 12vw;
        text-align: center;

        .title-icon {
            width: 4vw;

            img {
                width: 100%;
            }
        }

        h2 {
            color: #D2C2A8;
        }

        h3 {
            font-size: 1.5vw;
            color: #aea393;
            font-style: italic;
        }
    }

    .container {
        position: relative;
        width: 100%;
        height: 100vh;
        transform: scale(1);
        transform-origin: top left;

        svg * {
                transform-origin: 50%;
        }

        .goo-container {
            position: absolute;
            @keyframes left {
                0% {transform: none;}
                100% { transform: rotate(1turn) }
            }
            @keyframes right { 
                0% {transform: none;}
                100% { transform: rotate(-1turn) }
            }
           
            .goo-content {
                position: absolute;
                z-index: 1;
                width: max-content;
                top: 0;
                left: 0;

                h2 {
                    width: 300px;
                    text-align: left;
                    word-break: keep-all;
                    font-size: 60px;
                    line-height: 72px;
                    text-transform: capitalize;
                    transition: 0.3s ease;
                    color: white;

                    &:hover {
                        color: black;
                    }
                }

