.image-text-pagenation {
    .content-container {
        position: relative;
        width: 100%;
        height: 100vh;
        .content {
            width: 50%;
            height: 100vh;
            padding: 5vw 2vw 0 2vw;

            .caption-content {
                position: absolute;
                top: 2vw;
                left: 2vw;
                padding: 1vw 0;
                p {
                    font-weight: bold;
                }
            }

            .heading-content {
                padding: 1vw 0;

                .heading-large {
                    white-space: pre-wrap;
                }
            }
            
            .sub-heading-content {
                .sub-heading {
                    white-space : pre-wrap;
                }
            }

            .body-content {
                padding: 4vw 0;
                .body-large {
                    white-space : pre-wrap;
                }
            }
        }

        .content-align-center {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 100%;
            padding: 0 10vw;

            .heading-large,
            .sub-heading-content,
            .body-content {
                text-align: center;
                padding: 0.1vw 0;
            }
        }

        .content-theme-light {
            .body-small,
            .heading-large,
            .sub-heading,
            .body-large {
                color: white;
            }
        }

        .background-image-content {
            display: flex;
            justify-content: flex-end;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            z-index: -1;

            img {
                width: 100%;
                height: 100vh;
                object-fit: cover;
            }

            .image-half {
                width: 50%;
            }

            .image-fitten {
                width: auto;
                height: 100vh;
                object-fit: fill;
            }
        }

        .background-image-fitten {
            display: flex;
            justify-content: center;
            background: black;
        }
    }

    .pager {
        position: absolute;
        bottom: 48px;
        left: 0;
        width: 100%;

        .pager-container {
            display: flex;
            justify-content: center;
            gap: 1vw;
            align-items: center;
            padding: 12px 24px;
            border-radius: 50%;
            .arrow {
                width: 22px;
                height: 22px;
                cursor: pointer;
    
                img {
                    width : 100%;
                    height : 100%;
                }
            }
    
            .page-number {
                display: flex;
                align-items: center;
                gap: 12px;
    
                .body-large {
                    width: 24px;
                    text-align: center;
                }
    
                .break-line {
                    height: 1.4px;
                    width: 28px;
                    border: none;
                    background: #000;
                }
            }
        }
    }
}